import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CatalogoComponent } from './catalogo.component';
import { CatalogoRoutingModule } from './catalogo-routing.module';
import { SharedModule } from '../shared/shared.module';
import { DetailComponent } from './detail.component/detail.component.component';
import { CatalogoServiceService } from './catalogo-service.service';
import { CarouselModule } from 'angular4-carousel';

@NgModule({
  imports: [CommonModule, CatalogoRoutingModule, SharedModule, CarouselModule],
  declarations: [CatalogoComponent, DetailComponent],
  exports: [CatalogoComponent, DetailComponent],
  providers: [CatalogoServiceService],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class CatalogoModule { }



